/* CARDS */

.cards {
    width: 100%;
    height: 70vh;
    display: flex;
    /* flex-direction: column; */
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 20px 0;
    margin: 10px 15px;
    position: relative;
}


.btnInc {
    position: absolute;
    top: -20px;
    left: 55%;
    width: 10%;
    border-radius: 10px;
    padding: 5px;
 
}

.btnInc:hover {
    background: #000;
        color: #fff;
        cursor: pointer;
}


/* CARD */

.card {

    border: 1px solid #f3f3f3;
    padding: 20px;
    width: 210px;
    height: 300px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    transition: all 0.2s ease-in-out;
    margin: 3px;
}

.cardBtn {
    width: 100%;
    display:  flex;
    justify-content: space-between;
    align-items: center;
}

.cardBtn p {
    font-size: 12px;
}
.card:hover {
    cursor: pointer;
    transform: translateY(-5px);
    box-shadow: 0px 14px 35px rgba(0,0,0,0.06);
}