.navigate {
    width: 100%;
    display:  flex;
    column-gap: 20px;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}

.menu {
    text-transform: uppercase;
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding: 10px 20px;
    border-radius: 20px;
    font-weight: 600;
    font-size: 18px;
    cursor: pointer;
    text-decoration: none;
    transition: all 0.1s ease-in-out;
}
.menu:hover {
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
}

.active {
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
}