.header {
    width: 100%;
    height: 10vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: 30px 0 ;
    /* column-gap: 20px; */
   
}

.logo {
    display: flex;
    column-gap: 10px;
    align-items: center;
}

.logo img {
    width: 60px;
    border-radius: 40px;
}
.logo h2 {
    text-transform: uppercase;
}


/* Search */

.search {
    width: 30vw;
    flex-wrap: wrap;
    min-width: 300px;
    position: relative;
}

 .input {
    border: 1px solid rgba(0,0, 0, 0.1);
    padding: 12px 20px 12px 40px;
    width: 300px;
    border-radius: 10px;
    font-size: 16px;
}

.input:focus {
    border: 1px solid rgba(0, 0, 0, 0.2);;
}

.icon {
    width: 25px;
    height: 25px;
    opacity: 0.3;
    position: absolute;
    top: 8px;
    left: 10px;
}

.close {
    position: absolute;
    top: 10px;
    left: 265px;
    opacity: 0.3;
    cursor: pointer;
}



/* Location */

.contacts {
    width: 25vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

}

.location {
    display:  flex;
    column-gap: 5px;
    align-items: center;
    margin-right: 10px;
}

.iconMap {
    height: 30px;
}

.contact {
    display: flex;
    flex-direction: column;
}

.contact p {
    font-weight: 700;
    color: #c4bd34;
}




/* Inout Slider */

.range {
    width: 200px;
}

.rangeSlider {
    width: 100%;
    max-width: 200px;
    /* height: 50px; */
}
.track {
  margin: 0 10px;
    top: 20px;
    height: 10px;
    background:rgba(0, 0, 0, 0.2)
}

.track:first-child{
    background: #fff;
}
.track:nth-last-child(3){
    background: #fff;
}

.rangeSlider .thumb {
    cursor: pointer;
    margin: 10px 0;
    padding: 2px 5px;
    background: #414040;
    color:  #fff;
    /* border-radius: 50%; */
}