.pagination {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    padding: 50px 10px;
}

.li {
display: inline-block;
}
.a, .breakLeb {
text-align: center;
width: 40px;
line-height: 42px;
height: 40px;
border: 1px solid #0f0e0e;
border-radius: 30px;
margin-right: 10px;
cursor: pointer;
display: inline-block;
color: #140d0b
}

.active {
    background-color: #0a0705;
    color: #fff;
border-radius: 30px;
}

